@import url(//fonts.googleapis.com/css?family=Lato:300:400);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Global css used in all files! */

/* A list of colors can be found in ../components/style/colors.ts */

:root {
  --orange: #f66a0a;
  --black: #1b1f23;
  --snow_white: #fafbfc;
  --purple_link: #f9b3dd;
}

body {
  background-color: #1b1f23;
  background-color: var(--black);
  min-height: 75vh;
}

h1 {
  color: #fafbfc;
  color: var(--snow_white);
}

p {
  color: #fafbfc;
  color: var(--snow_white);
}

a {
  color: #f9b3dd;
  color: var(--purple_link);
}

.home {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.home h1 {
  color: var(--orange);
  margin-top: 3rem;
}

.home h1:hover {
  text-decoration: overline;
}

.notFound {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.notFound h1 {
  color: rgb(233, 157, 169);
}

.footer {
  position: relative;
  text-align: center;
  /* background: linear-gradient(
		60deg,
		rgba(84, 58, 183, 1) 0%,
		rgba(0, 172, 193, 1) 100%
	); */
  color: var(--snow_white);
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
}

.content_flex p {
  font-family: "Lato", sans-serif;
  letter-spacing: 1px;
  font-size: 14px;
  color: var(--snow_white);
  /* Neon affect on header text */
  text-shadow:
    0 0 7px var(--snow_white),
    0 0 10px var(--snow_white),
    0 0 21px var(--snow_white),
    0 0 42px var(--orange),
    0 0 82px var(--orange),
    0 0 92px var(--orange),
    0 0 102px var(--orange),
    0 0 151px var(--orange);
}

.content_flex p:hover {
  font-size: 15px;
  transition: 0.7s;
}

.waves {
  position: relative;
  width: 100%;
  height: 15vh;
  margin-bottom: -7px; /*Fix for safari gap*/
  min-height: 100px;
  max-height: 150px;
}

.content {
  position: relative;
  height: 20vh;
  text-align: center;
  background-color: var(--snow_white);
}

/* Animation */

.parallax > use {
  animation: move-forever 25s cubic-bezier(0.55, 0.5, 0.45, 0.5) infinite;
}
.parallax > use:nth-child(1) {
  animation-delay: -2s;
  animation-duration: 7s;
}
.parallax > use:nth-child(2) {
  animation-delay: -3s;
  animation-duration: 10s;
}
.parallax > use:nth-child(3) {
  animation-delay: -4s;
  animation-duration: 13s;
}
.parallax > use:nth-child(4) {
  animation-delay: -5s;
  animation-duration: 20s;
}
@keyframes move-forever {
  0% {
    transform: translate3d(-90px, 0, 0);
  }
  100% {
    transform: translate3d(85px, 0, 0);
  }
}
/*Shrinking for mobile*/
@media (max-width: 768px) {
  .waves {
    height: 40px;
    min-height: 40px;
  }
  .content {
    height: 30vh;
  }
  h1 {
    font-size: 24px;
  }
}

